import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {API_STOCKRELOCATION_IMPORT} from 'api'
import {Import} from 'HOC'

@connect(
  state => ({...state.stockRelocationLog}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {handleGetList, searchParams, page, size} = this.props
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T05')
        }}>模板下载</Button>
        <Import
          action={{api: API_STOCKRELOCATION_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>批量导入</Button>
        </Import>
      </div>
    )
  }
}



