import * as actionType from './actionType'
import {getData} from 'util'
import {API_STOCKRELOCATION_LOGS_PAGE, API_STOCKRELOCATION_GETAVAILABLEQTY, API_STOCKRELOCATION_EXCUTERELOCATIONLOG, API_STOCKRELOCATION_JUDGETOSTORAGELOCATION, API_STOCKRELOCATION_CREATEPRODUCTSTOCKRELOCATIONLOG} from 'api'
//同步
export const update = (object) => ({
  type: actionType.STOCKRELOCATIONLOG_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.STOCKRELOCATIONLOG_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_STOCKRELOCATION_LOGS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_FILE
    })
  })
}

// 获取储位可用量
export const handleGetGoodsInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STOCKRELOCATIONLOG_UPDATE, data: {createTableLoading: true}})
  let api = API_STOCKRELOCATION_GETAVAILABLEQTY
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack&&callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_FILE
    })
  })
}

// 判断重定位至储位是否合法
export const handleJudge = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STOCKRELOCATIONLOG_UPDATE, data: {tableLoading: true}})
  let api = API_STOCKRELOCATION_JUDGETOSTORAGELOCATION
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_UPDATE,
      data: {
        tableLoading: false
      }
    })
    callBack&&callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_FILE
    })
  })
}

// 执行
export const handleCarry = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STOCKRELOCATIONLOG_UPDATE, data: {tableLoading: true}})
  let api = API_STOCKRELOCATION_EXCUTERELOCATIONLOG
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_UPDATE,
      data: {
        tableLoading: false,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_FILE
    })
  })
}

// 创建
export const handleCommit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STOCKRELOCATIONLOG_UPDATE, data: {tableLoading: true, modalTuttonLoading: true}})
  let api = API_STOCKRELOCATION_CREATEPRODUCTSTOCKRELOCATIONLOG
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_UPDATE,
      data: {
        tableLoading: false,
        modalTuttonLoading: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKRELOCATIONLOG_FILE
    })
  })
}


