import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES, API_STOCKRELOCATION_KEYVALUES, API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.stockRelocationLog, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  // 新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新建库存重定位', modalData: ''})
  }
  render() {
    const {
      searchParams: {
        // status,
        shipperCode,
        orderCode,
        productCode,
        productName,
        sourceReservoirAreaCode,
        destReservoirAreaCode
      },
      userLoation
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
              label='货主'
              selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>单号：</label>
              <div className='box'>
                <Input value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              // style={{width: '60%'}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              searchParamsKey='productCode'
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }} />
            </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES}
              label='来源库区'
              selectItem={{data: sourceReservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              searchParamsKey='reservoirAreaCode'
              onChangeSelect={(value) => {
                this.onInputChange({sourceReservoirAreaCode: value})
              }}
              onSelect={(value) => {
                this.onInputChange({sourceReservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES}
              label='目的库区'
              selectItem={{data: destReservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              searchParamsKey='reservoirAreaCode'
              onChangeSelect={(value) => {
                this.onInputChange({destReservoirAreaCode: value})
              }}
              onSelect={(value) => {
                this.onInputChange({destReservoirAreaCode: value})
              }}
            />
          </Col>
          {/* <Col span={8}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_KEYVALUES}
              label='状态'
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col> */}
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" onClick={this.handleAdd}>新建</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



