import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  message
} from 'antd'
import {CsxFormSelect, CsxFormSearch} from 'Form'
import {API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES, API_STOCKRELOCATION_SHIPPERKEYVALUES, API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES, API_PRODUCTSTOCKDETAIL_GETPRODUCTKEYVALUES, API_STORAGELOCATIONS_GETTOSTORAGELOCATIONKEYVALUE, API_STOCKRELOCATION_JUDGETOSTORAGELOCATION} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.stockRelocationLog}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    
  }
  handleSelectChange = (value) => {
    const {form, update, modalData} = this.props
    if(modalData.sourceStoreLocationCode && value.sourceStoreLocationCode) {
      if(value.sourceStoreLocationCode !== modalData.sourceStoreLocationCode) {
        update({modalData: {...modalData, ...value, productCode: '', remark: ''}})
        return
      }
    }
    update({modalData: {...modalData, ...value}})
  }

  handleSubmit = (e) => {
    const {form, modalData, handleCommit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log(values)
        let reqObj = {...values}
        reqObj.destStoreLocationName = reqObj.destStoreLocationCode.value
        reqObj.destStoreLocationCode = reqObj.destStoreLocationCode.children
        reqObj.sourceStoreLocationName = reqObj.sourceStoreLocationCode.value
        reqObj.sourceStoreLocationCode = reqObj.sourceStoreLocationCode.children

        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'shipperCode') {
              reqObj.shipperName = reqObj[i].children
            }
            if (i == 'sourceReservoirAreaCode') {
              reqObj.sourceReservoirAreaName = reqObj[i].children
            }
            if (i == 'destReservoirAreaCode') {
              reqObj.destReservoirAreaName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }
        console.log(reqObj)
        let result = {
          data: reqObj
        }, msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleCommit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
          this.handleCloseModal()
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    console.log(modalData)
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16}
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='777px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="货主"
                hasFeedback
              >
                {getFieldDecorator('shipperCode', {
                  initialValue: modalData.shipperCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                    selectItem={{key: 'key', valueKey: 'value'}}
                    handleSelectChange={(value) => {
                      this.handleSelectChange({shipperCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="从库区"
                hasFeedback
              >
                {getFieldDecorator('sourceReservoirAreaCode', {
                  initialValue: modalData.sourceReservoirAreaCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES}
                    selectItem={{key: 'key', valueKey: ['key', 'value']}}
                    handleSelectChange={(value) => {
                      this.handleSelectChange({sourceReservoirAreaCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="从储位"
                hasFeedback
              >
                {getFieldDecorator('sourceStoreLocationCode', {
                  initialValue: modalData.sourceStoreLocationCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
                    searchParams={{reservoirAreaCode: modalData.sourceReservoirAreaCode && modalData.sourceReservoirAreaCode.value}}
                    selectItem={{key: 'value', valueKey: 'key'}}
                    errMessage='请先选择从库区'
                    handleSelectChange={(value) => {
                      this.handleSelectChange({sourceStoreLocationCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="商品编码"
              >
                {getFieldDecorator('productCode', {
                  initialValue: modalData.productCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSearch
                    dateApi={API_PRODUCTSTOCKDETAIL_GETPRODUCTKEYVALUES}
                    searchParamsKey='productCode'
                    searchParams={{shipperCode: modalData.shipperCode && modalData.shipperCode.value, storeLocationCode: modalData.sourceStoreLocationCode && modalData.sourceStoreLocationCode.children, reservoirAreaCode: modalData.sourceReservoirAreaCode && modalData.sourceReservoirAreaCode.value}}
                    selectItem={{data: modalData.productCode, key: 'productCode', valueKey: ['productCode', 'productName']}}
                    onChangeSelect={(value) => {
                      this.handleSelectChange({productCode: value})
                    }}
                    onSelect={(value) => {
                      const {handleGetGoodsInfo, update} = this.props
                      this.handleSelectChange({productCode: value})
                      handleGetGoodsInfo({searchParams: {productCode: value.value, shipperCode: modalData.shipperCode && modalData.shipperCode.value, storeLocationCode: modalData.sourceStoreLocationCode && modalData.sourceStoreLocationCode.children}, errMessage: '请先选择货主，库区与储位'}, (data) => {
                        this.handleSelectChange({remark: data.availableQty, unit: data.unit})
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="可移动数量"
              >
                {getFieldDecorator('remark', {
                  initialValue: modalData.remark
                })(
                  <label>{modalData.remark}</label>
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="至库区"
                hasFeedback
              >
                {getFieldDecorator('destReservoirAreaCode', {
                  initialValue: modalData.destReservoirAreaCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STOCKRELOCATION_RELOCATIONRESERVOIRKEYVALUES}
                    selectItem={{key: 'key', valueKey: ['key', 'value']}}
                    handleSelectChange={(value) => {
                      this.handleSelectChange({destReservoirAreaCode: value})
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="至储位"
                hasFeedback
              >
                {getFieldDecorator('destStoreLocationCode', {
                  initialValue: modalData.destStoreLocationCode,
                  rules: [{
                    required: true, message: '请填写!'
                  }]
                })(
                  <CsxFormSelect
                    dateApi={API_STORAGELOCATIONS_GETTOSTORAGELOCATIONKEYVALUE}
                    searchParams={{reservoirAreaCode: modalData.destReservoirAreaCode && modalData.destReservoirAreaCode.value, productCode: modalData.productCode && modalData.productCode.value, shipperCode: modalData.shipperCode && modalData.shipperCode.value}}
                    selectItem={{key: 'value', valueKey: 'key'}}
                    errMessage='请先选择至库区跟商品'
                    handleSelectChange={(value) => {
                      const {handleJudge} = this.props
                      handleJudge({searchParams: {
                        reservoirAreaCode: modalData.destReservoirAreaCode && modalData.destReservoirAreaCode.value, 
                        storageLocationCode: value && value.value,
                        shipperCode: modalData.shipperCode && modalData.shipperCode.value,
                        productCode: modalData.productCode && modalData.productCode.value
                      }}, () => {
                        console.log(value)
                        this.handleSelectChange({destStoreLocationCode: value})
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={8}>
              <FormItem
                {...formItemLayout}
                label="移动数量"
                hasFeedback
              >
                {getFieldDecorator('moveQty', {
                  initialValue: modalData.moveQty,
                  rules: [{
                    required: true, message: '请输入正确的数量', pattern:  (modalData.unit && modalData.unit.toUpperCase()) == 'KG' ? /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,3})?$/ : /^[1-9]\d*$/
                  }]
                })(
                  <Input />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



